.HomePage {
  background: radial-gradient(#171717, #2b2b2b);
  height: 100vh;
  font-size: 3rem;
  color: #ff1818;
  letter-spacing: 3.5px;
  display: grid;
  align-content: center;
  text-align: center;
}

.gameflix-title {
  margin: 0px;
  animation: slideInTop 4s ease 0s 1 forwards;
}

.underline {
  animation: openAnimate 4s ease 0s 1 forwards;
  border-bottom: 3px red solid;
  width: 0%;
  margin: auto;
}

.mission-statement {
  animation: slideInBottom 4s ease 0s 1 forwards;
  padding: 3% 0%;
  font-weight: 400;
  text-align: center;
  font-size: 22px;
  margin: auto;
}

.enter-button {
  animation: slideInBottom 4s ease 0s 1 forwards;
  display: block;
  margin: 0 auto;
  background: #ff1818;
  font-size: 1rem;
  display: grid;
  color: #141414;
  font-weight: 800;
  align-content: center;
  justify-content: center;
  height: 100px;
  width: 100px;
  border-radius: 50%;
  transition: all 1s ease;

  &:hover {
    cursor: pointer;
    height: 110px;
    width: 110px;
    border: solid darkred 1px;
  }
}

// animation for GameFlix title 
@keyframes slideInTop {
  0% {
    transform: translateY(-600px);
  }

  50% {
    transform: translateX(0px);
  }
}

// animation for mission statement & enter button
@keyframes slideInBottom {
  0% {
    transform: translateY(600px);
  }

  50% {
    transform: translateX(0px);
  }
}

// animation for GameFlix underline
@keyframes openAnimate {
  50% {
    width: 0%
  }

  100% {
    width: 40%
  }
}