* {
  margin: 0px;
  padding: 0px;
  font-family: Open Sans, sans-serif; }

.App {
  text-align: center;
  height: 100vh; }

::-webkit-scrollbar {
  display: none; }

.Navbar {
  background-image: linear-gradient(#5b5b5b, #2b3034); }
  .Navbar .main-nav {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    min-height: 75px; }

.filter-buttons {
  display: flex;
  padding-left: 7px;
  margin: 26px 26px; }

.title {
  color: #ff1818;
  letter-spacing: 3.5px; }
  .title:hover {
    cursor: pointer; }

.nav-btn {
  font-size: 15px;
  color: #FFF;
  padding: 7px 0px 7px 40px;
  display: inline-block; }
  .nav-btn:hover {
    cursor: pointer;
    font-weight: bold; }

.search-field {
  display: flex;
  margin: 16px 28px; }
  .search-field .fa-search {
    font-size: 20px;
    color: #FFF;
    margin: 2%;
    padding-top: 8px; }

.searchbar {
  width: 200px;
  height: 30px;
  font-size: 15px;
  padding-left: 7px;
  border: none;
  border-bottom: 2.5px solid #FFF;
  background-color: transparent;
  color: #FFF;
  margin: 2%; }
  .searchbar:focus {
    outline: 0; }

::placeholder {
  color: #d2cfcf;
  opacity: 1;
  font-size: 15px; }

@media screen and (max-width: 1030px) {
  .main-nav {
    flex-direction: column-reverse; }
  .filter-buttons {
    justify-content: center; }
  .search-field {
    justify-content: center;
    margin: 0px; }
  .searchbar {
    width: 285px; } }

@media screen and (max-width: 720px) {
  .title {
    padding-top: 15px; }
  .filter-buttons {
    margin: 5px 0px; }
  .nav-btn {
    display: block; } }

@media screen and (max-width: 465px) {
  .AdvancedSearch,
  .advanced-search {
    display: none; }
  .filter-buttons {
    flex-direction: column; }
  .title {
    padding: 0px; }
  .nav-btn {
    padding: 7px 0px; } }

.AdvancedSearch {
  width: 100%;
  color: whitesmoke;
  background-color: transparent;
  height: 0px;
  overflow: hidden;
  transition: .2s linear; }

.AdvancedSearchClicked {
  transition: .2s linear;
  height: 160px; }

.advanced-search-form {
  display: flex;
  justify-content: space-evenly;
  padding: 1% 3% 1.5% 3%; }

.filter-container {
  padding: 0px 20px;
  text-align: left; }
  .filter-container .filter-title {
    color: #f7dd7d;
    font-size: 14px;
    letter-spacing: 3px;
    padding-bottom: 5px;
    font-weight: 600; }

.checkbox-and-label {
  padding-bottom: 10px; }
  .checkbox-and-label label {
    font-size: 15px;
    font-weight: 300;
    padding-left: 10px; }

.Featured {
  height: 60%; }

.LandingPage {
  height: 100%;
  overflow: scroll; }

.Carousel {
  background-color: #15171a;
  align-content: center;
  display: grid;
  grid-template-columns: 1fr 50fr 1fr;
  grid-template-areas: "genre genre genre" "left mid right" "popup popup popup"; }

.genre {
  grid-area: genre;
  margin: 0px;
  text-align: left;
  font-size: 22px;
  font-weight: 600;
  color: #FFF;
  padding: 23px 0px 0px 38px; }

.scroll-container {
  grid-area: mid;
  display: flex;
  overflow: scroll; }

.side-carrot {
  font-size: 42px;
  color: #FFF;
  display: inline-block;
  padding: 138px 0 138px 10px;
  z-index: 1; }
  .side-carrot:hover {
    cursor: pointer; }

.Carousel i {
  visibility: hidden; }

.Carousel:hover i {
  visibility: visible; }

.fa-angle-left {
  grid-area: left; }

.fa-angle-right {
  grid-area: right;
  padding-right: 10px; }

.hide {
  display: none; }

.PopUp {
  grid-area: popup; }

.game-card {
  background: #FFF;
  border: outset 4px #2b3034;
  color: #fff;
  height: 250px;
  padding-bottom: 22px;
  border-radius: 5px;
  width: 264px;
  margin: 15px 10px 5px 10px;
  transition: all 200ms ease; }
  .game-card:hover {
    cursor: pointer;
    position: relative;
    box-shadow: 6px 7px 12px #646262; }

.game-card-header-container {
  position: relative;
  height: 60px;
  margin: auto;
  background-image: linear-gradient(#5b5b5b, #2b3034); }
  .game-card-header-container .game-card-header {
    display: block;
    padding: 12px 27px;
    font-weight: 600;
    font-size: 15px; }

.game-card-image {
  height: 180px;
  max-width: 400px;
  padding-top: 18px; }

.PopUp {
  background-color: whitesmoke;
  display: grid;
  grid-template-columns: 1fr 1fr;
  animation: openPopUp 0.4s ease-out forwards;
  padding: 10px; }

.search-popup {
  background-color: #fff;
  color: #000;
  border: 6px solid black;
  border-radius: 5px;
  position: fixed;
  width: 90%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 1%;
  margin-top: 5%; }

.game-details {
  position: relative; }

.fa-times {
  display: inline;
  position: absolute;
  top: 5px;
  left: 5px;
  color: #000;
  font-size: 22px;
  border: 2px solid #000;
  padding: 2px 5px;
  border-radius: 100px;
  background-color: whitesmoke;
  transition: all 400ms ease; }
  .fa-times:hover {
    cursor: pointer;
    color: #000;
    background-color: #a2a2a2; }

.game-name {
  display: inline-block;
  font-size: 32px;
  margin: 0px auto;
  font-weight: 500;
  letter-spacing: 1px;
  padding: 5px 0px;
  border-bottom: 2px black solid;
  width: 84%;
  font-family: Staatliches, sans-serif; }

.game-info {
  display: flex;
  justify-content: space-evenly;
  border-bottom: 2px black solid;
  width: 84%;
  padding: 10px 0px;
  margin: 0px auto 10px auto; }

.popup-icon {
  display: inline-block; }

.publisher {
  display: block;
  font-size: 10px; }

.difficulty-level {
  border-bottom: 2px black solid;
  border-top: 2px black solid;
  margin: 10px auto;
  width: 84%;
  padding: 8px 0px;
  text-transform: capitalize; }

.temp-icon {
  font-size: 18px;
  padding-left: 10px; }

.fa-thermometer-quarter {
  color: green; }

.fa-thermometer-half {
  color: orange; }

.fa-thermometer-full {
  color: red; }

.game-description {
  text-align: justify;
  width: 83%;
  max-width: 530px;
  margin: 10px auto;
  font-size: 15px; }

.popup-image {
  height: 15rem; }

.youtube-video {
  object-fit: contain;
  height: 500px; }

@keyframes openPopUp {
  0% {
    height: 0px; }
  100% {
    height: 500px; } }

@media screen and (max-width: 1030px) {
  .popup-icon,
  .game-info,
  .difficulty-level,
  .game-description,
  .publisher {
    font-size: 12px; }
  .game-name {
    font-size: 30px; } }

@media screen and (max-width: 880px) {
  .PopUp {
    grid-template-columns: 1fr; }
  .youtube-video {
    display: none; } }

.SearchPage {
  color: #fff;
  height: 90%;
  overflow: scroll;
  background: #151719; }
  .SearchPage .carousel-game {
    padding: 0px 12px; }
  .SearchPage .all-search-cards {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(270px, 1fr));
    padding: 0% 2%; }
  .SearchPage .search-results-counter {
    font-size: 1rem;
    letter-spacing: 1px;
    color: whitesmoke;
    text-align: left;
    padding: 10px 0px 0px 50px;
    font-weight: 500; }

@media screen and (max-width: 880px) {
  .SearchPage .search-popup {
    margin-top: 1%; }
  .youtube-video {
    display: none; } }

.HomePage {
  background: radial-gradient(#171717, #2b2b2b);
  height: 100vh;
  font-size: 3rem;
  color: #ff1818;
  letter-spacing: 3.5px;
  display: grid;
  align-content: center;
  text-align: center; }

.gameflix-title {
  margin: 0px;
  animation: slideInTop 4s ease 0s 1 forwards; }

.underline {
  animation: openAnimate 4s ease 0s 1 forwards;
  border-bottom: 3px red solid;
  width: 0%;
  margin: auto; }

.mission-statement {
  animation: slideInBottom 4s ease 0s 1 forwards;
  padding: 3% 0%;
  font-weight: 400;
  text-align: center;
  font-size: 22px;
  margin: auto; }

.enter-button {
  animation: slideInBottom 4s ease 0s 1 forwards;
  display: block;
  margin: 0 auto;
  background: #ff1818;
  font-size: 1rem;
  display: grid;
  color: #141414;
  font-weight: 800;
  align-content: center;
  justify-content: center;
  height: 100px;
  width: 100px;
  border-radius: 50%;
  transition: all 1s ease; }
  .enter-button:hover {
    cursor: pointer;
    height: 110px;
    width: 110px;
    border: solid darkred 1px; }

@keyframes slideInTop {
  0% {
    transform: translateY(-600px); }
  50% {
    transform: translateX(0px); } }

@keyframes slideInBottom {
  0% {
    transform: translateY(600px); }
  50% {
    transform: translateX(0px); } }

@keyframes openAnimate {
  50% {
    width: 0%; }
  100% {
    width: 40%; } }

